
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import MyCell from './components/MyCell'
import Vue from 'vue';
import {Layout} from 'vue-ydui/dist/lib.rem/layout';
import {TabBar, TabBarItem} from 'vue-ydui/dist/lib.rem/tabbar';
import {Icons} from 'vue-ydui/dist/lib.rem/icons';

Vue.component(Icons.name, Icons)
Vue.component(TabBar.name, TabBar);
Vue.component(TabBarItem.name, TabBarItem);
Vue.component(Layout.name, Layout);

export default {
name: 'News',
components: {
MyCell,
XHeader

},
data(){
return{
realname:window.localStorage.getItem("realname"),
org_name:window.localStorage.getItem("org_name")
}
}
}



