
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'CaseContent',
components: {
XHeader
},
data () {
return {

list: []
}

},
mounted() {
this.a()
},
methods:{
a(){
var token=window.localStorage.getItem("access_token")
var id = this.$route.params.id
this.$axios.get('/api/v1/article/'+id,{params:{ 'access-token': token}}).then(res=>{

this.list = res.data

})
}

}

}



